import post_classes from './Post.module.css'
import './../../../../null.css'
let Post = (props) => {
    return (
        <div className={post_classes.item}>

            <div className={post_classes.avaPost}> <img src="data:image/jpeg;base64," /></div>
            <div>
                <div> {props.message}</div>
                <div>
                    <button><span>like {props.likesCount}</span></button>
                </div>
            </div>
        </div>
    )
}

export default Post